/* 회원가입 폼 컨테이너 */
.signup {
  width: 400px;
  margin: 80px auto; 
  padding: 40px;
  text-align: center;
  background-color: #f9f9f9; /* 밝은 배경색 */
  border-radius: 12px; /* 둥근 모서리 */
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15); /* 부드러운 그림자 */
  
  /* 기본 상태에서의 전환 설정 */
  transition: transform 1s ease-in-out, opacity 0.6s ease-in-out; /* 회전과 페이드 효과 추가 */
  transform-style: preserve-3d; /* 입체적인 회전 효과를 유지 */
  backface-visibility: hidden; /* 뒷면이 보이지 않도록 설정 */
}

/* 제목 스타일 */
.signup h1 {
  margin-bottom: 30px;
  font-size: 26px;
  font-weight: bold;
  color: #333; /* 다크 그레이 색상 */
}

/* 입력 필드 스타일 */
.input-field {
  display: block;
  width: calc(100% - 24px); /* 패딩을 고려한 너비 */
  margin-bottom: 20px;
  padding: 12px;
  font-size: 16px; /* 폰트 크기 조정 */
  border-radius: 8px; /* 둥근 모서리 */
  border: 1px solid #ccc; /* 연한 회색 테두리 */
}

/* 입력 필드 포커스 시 스타일 */
.input-field:focus {
  outline: none; /* 기본 아웃라인 제거 */
  border-color: #007BFF; /* 포커스 시 파란색 테두리 */
}

/* 체크박스와 라벨 컨테이너 정렬 */
.checkbox-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.checkbox-container input[type="checkbox"] {
  margin-right: 10px; /* 체크박스와 텍스트 사이 간격 조정 */
}

.checkbox-container label {
  font-size: 14px;
  color: #666; /* 연한 회색 텍스트 색상 */
}

/* 버튼 스타일 */
.signup-button {
 width: calc(100% -24px);
 margin-top:20px;
 padding:12px;
 font-size:16px;
 font-weight:bold;
 color:white; /* 흰색 글씨 */
 background-color:#ff4d4d; /* 기본 빨간색 배경 */
 border:none;
 border-radius:8px; /* 둥근 모서리 */
 cursor:pointer;

 transition: background-color .3s ease, transform .2s ease, box-shadow .2s ease; /* 부드러운 전환 효과 추가 */
}

/* 버튼 호버 시 스타일 (마우스 올릴 때) */
.signup-button:hover {
 background-color:#e60000; /* 진한 빨간색으로 변경 */
 transform: translateY(-3px); /* 살짝 위로 이동하는 효과 */
}

/* 버튼 클릭 시 스타일 (마우스 클릭할 때) */
.signup-button:active {
 transform: translateY(0); 
}

/* 회전 애니메이션 클래스 추가 (Y축 기준으로 더 부드럽게 회전) */
.rotate-out {
 transform: rotateY(180deg); /* Y축을 기준으로 회전하는 애니메이션 적용 (입체적) */
 opacity: 0; /* 페이드 아웃 효과 추가 (회전과 함께 사라짐) */
}